import { Component, OnInit, OnDestroy } from '@angular/core';
import { AuthUserService } from 'src/app/core/auth-user.service';
import { IUser } from 'src/app/core/user.model';
import { DashboardService } from './dashboard.service';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit, OnDestroy {
  private ngUnsubcribe = new Subject();

  sidebarOpen = false;
  profileDropDown = false;
  user: IUser;
  adminStates: any;
  vendorStates: any;
  cuisines = ['North Indian', 'Fast Food', 'Chinese', 'South Indian', 'Beverages', 'Mithai', 'Cafe', 'Desserts', 'Bakery', 'Pizza', 'Seafood'];
  constructor(private authUserService: AuthUserService, private dashboard: DashboardService) { }

  ngOnInit(): void {
    this.user = this.authUserService.user;
    if (this.user && this.user.role == 'Admin' || this.user.role == 'Manager') {
      this.dashboard.getStatsForAdmin()
        .pipe(takeUntil(this.ngUnsubcribe))
        .subscribe((data: any) => {
          this.adminStates = data;
        });
    } else {
      this.dashboard.getStatsForVendor(this.user._id)
        .pipe(takeUntil(this.ngUnsubcribe))
        .subscribe((data: any) => {
          this.vendorStates = data;
        });
    }
  }

  ngOnDestroy(): void {
    this.ngUnsubcribe.next();
    this.ngUnsubcribe.complete();
  }

  logout(): void {
    this.authUserService.logout();
  }

  // addTopVendorForCategory(cuisine): void {
  //   console.log('cuisine: ', cuisine);
  //   const modalRef = this.modalService.open(AddTopVendorForCategoryModelComponent, {
  //     size: 'lg'
  //   });
  //   modalRef.componentInstance.cuisine = cuisine;

  // }
}
