import { Injectable } from '@angular/core';
import { Observable, BehaviorSubject } from 'rxjs';
import { DashboardApiService } from '../core/dashboard.service';

@Injectable()
export class DashboardService {

  private _vendorList$ = new BehaviorSubject<any[]>(null);

  get vendorList$(): Observable<any[]> {
    return this._vendorList$.asObservable();
  } constructor(private dashboardService: DashboardApiService){ }

  getStatsForAdmin(): Observable<any> {
    return this.dashboardService.getStatsForAdmin();
  }

  getStatsForVendor(id: string): Observable<any> {
    return this.dashboardService.getStatsForVendor(id);
  }
}
